.home_title{
    font-size: 35px;
    color: white;
    /* display: block; */
    margin: 10px 0;
    padding: 0px 25px;
    /* display: inline-flex; */
    /* text-align: center; */
}

.home_subtitle{
    font-size: 24px;
    color: white;
    margin: 10px 0 0 0;
    padding: 0 25px;
}

.home_units{
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: 20px 0 0 0;
}

.home_unit{
    width: 140px;
    border: 1px solid white;
    border-radius: 10px;
    margin: 0 0 20px 20px;
}

.unit_link{
    /* flex-grow: 3; */
    width: 20%;
    height: 80px;
    margin-bottom: 10px;
    margin-left: 15px;
    flex-wrap: wrap;
    display: flex;
    justify-content: space-around;
    text-decoration: none;
    border: 2px solid white;
    border-radius: 10px;
    transition: background-color 900ms ease-in-out;
}

.unit_link:hover{
    background-color: black;
}

.unit_link:focus{
    outline: none;
}

.unit_icon{
    /* height: 100%; */
    /* border: 1px solid red; */
    width: 40px;
    display: flex;
    align-self: center;
    border-radius: 10px;
    padding: 8px;
    /* margin: 10% 0 0 0; */
}

.unit_name{
    font-size: 20px;
    font-weight: bold;
    color: white;
}





