.hidden_menu{
    position: fixed;
    margin-top: -30px;
    margin-left: -28px;
    background-color: #8E8DE0;
    border-radius: 25px;
    width: 20%;
    height: 91vh;
}

.list_items{
    list-style: none;
    margin-top: 10px;
}

.menu_list_item{
    color: #fff;
    text-decoration: none;
    font-size: 24px;
    font-style: italic;
}

@media only screen and (max-width: 600px){
    .hidden_menu{
        width: 80%;
        height: 99.9%;
        /* border-radius: 25px; */
    }
}