.menu{
    color: #fff;
    margin: 30px 25px 15px 25px;
    cursor: pointer;
    display: inline-flex;
    width: 38px;
    flex-wrap: wrap;
    position: relative;
    cursor: pointer;
}

.menu_line{
    transform: rotate(-15deg);
    width: 30px;
    margin: 3px 4px;
    border: 1px solid #fff;
    border-radius: 25px;
    height: 1px;
    display: flex;
    background-color: white;
    cursor: pointer;
}

@media only screen and (max-width: 600px){
    .menu{
        width: 40px;
    }
    
    .menu_line{
        width: 100%;
    }
}