  body{
    font-family: Lato;
    background-image: url("../assets/bg5.png");
    display: flex;
    justify-content: center;
  }

  .wrapper{
    width: 450px;
    max-width: 450px;
    margin: 3vh auto 3vh auto;
    background-image: url("../assets/bg.png");
    background-repeat: no-repeat; 
    height: 90vh;
    border-radius: 20px;
  }

    .wrap_input{
      display: flex;
      justify-content: center;
      width: 80%;
      margin: 0 auto;
    }
    
    .input{
      display: flex;
      margin: 5px 0 0 0;
      width: 100%;
      height: 50px;
      font-size: 20px;
      padding: 0 10px;
      border: 1px solid black;
      border-radius: 10px;
    }
    
    .input[type=number]::-webkit-inner-spin-button, 
    .input[type=number]::-webkit-outer-spin-button{ 
      -webkit-appearance: none; 
      appearance: none;
      margin: 0;
    }
    
    .input[type=number]{
      -moz-appearance: textfield;
    }
    
    .dropdown_list{
      margin: 8px 0 0 10px;
      width: 85px;
      display: flex;
      align-self: center;
      height: 45px;
      font-size: 20px;
      background-color: transparent;
      border-radius: 10px;
      border: none;
      margin-left: -90px;
    }
    
    .input:focus,
    .dropdown_list:focus,
    .btn_convert{
      outline:  none;
    }

    @media only screen and (max-width: 600px){
      body{
        font-family: Lato;
        background: rgb(131, 129, 240);
        display: flex;
        justify-content: center;
      }

      .wrapper{
        width: 100vw;
        height: 100vh;
        margin: 0 auto;
      }
    }