  .display{
    display: flex;
    width: 90%;
    height: 53vh;
    overflow-y: scroll;
    overflow-x: hidden;
    flex-direction: column;
    justify-content: flex-start;
    margin: 50px 0 20px 25px;
    color: white;
  }

  ::-webkit-scrollbar {
    width: 10px; 
    background: #5242E6; 
    border-radius: 20px;
    margin-left: 10px;
    border-bottom-right-radius: 20px;
    }

  ::-webkit-scrollbar-thumb {
      background:  #FFFFFF;
      border-radius: 20px;
      margin-left: 10px;
    }
  
  .display_unit{
    font-size: 18px;
    color: white;
    border-bottom: 2px solid black;
    padding: 10px 5px; 
  }
  
  .btn_convert{
    margin: 5px 25%;
    width: 50%;
    color: white;
    background-color: transparent;
    border: 2px solid white;
    border-radius: 10px;
    height: 50px;
    font-size: 20px;
  }
  
  .btn_convert:hover{
    border: 2px solid black;
  }
  
  /* and (min-width:250px) and  */
  @media only screen and (max-width: 600px){
    
    .wrap_input{
      width: 100%;
      flex-wrap: wrap;
    }
    .input{
      width: 80%;
    }
    .btn_convert{
      width: 50%;
      margin: 5px 25%;
    }
    .dropdown_list{
      width: 85px;
      margin-left: -90px
    }
    .display{
      width: 80%;
    }
  }